@import "./variables.scss";

header.header {
  width: 100%;
  height: 5rem;
  background-color: $color-primary;
  border-bottom: 1px solid $color-accent;
  display: flex;
  align-items: center;
  padding-left: 1rem;
}

main.wrapper {
  width: 100%;
  min-height: calc(100vh - 5rem);
  background-color: #eff7ff;
}

.content {
  width: 80%;
  margin: 5rem auto;
  background-color: #fff;
  border: 1px solid #e9e9e9;
  border-radius: 0.3rem;
  padding: 2rem;
}

#subreddit-input {
  :first-child {
    margin-bottom: 1rem;
  }

  button {
    margin-right: 1rem;
  }
}

.text-input {
  width: 100%;
  border: none;
  border-bottom: 2px solid $color-primary;
  font-size: 1.6rem;
  padding: 0.75rem;
  background-color: #eeeded;

  &:focus {
    outline: none;
  }
}

.heading {
  &--title {
    font-size: 3.2rem;
  }

  &--subtitle {
    font-size: 2rem;
  }

  &--normal {
    font-size: 1.6rem;
  }
}

.btn {
  border-radius: 0.3rem;

  &--primary {
    background-color: $color-primary;
    border: none;
    border-right: 1px solid $color-accent;
    border-left: 1px solid $color-accent;
    border-bottom: 2px solid $color-accent;
    color: $color-text-normal;

    &:hover {
      cursor: pointer;
      background-color: lighten($color-primary-light, 0.2);
    }
  }

  &--normal {
    padding: 0 1.3rem;
    height: 3.5rem;
  }
}

section#subreddit-posts {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
}

.post {
  position: relative;
  padding: 0.4rem;
  width: 100%;
  display: flex;
  flex-direction: column;

  .favourite {
    top: -1rem;
    right: 1rem;
    position: absolute;
    font-size: 1.3rem;

    input {
      margin-right: 0.5rem;
    }
  }

  .title {
    height: 3rem;
    font-size: 1.6rem;
    font-weight: 500;
  }

  .content {
    width: 100%;
    font-size: 1.4rem;
    overflow: hidden;

    img {
      max-height: 35rem;
      width: auto;
    }
  }
}
